import React from 'react';

function Card ({header, content}) {

    return (
        <div className="card border-left-midnight shadow py-2">
            <div className="card-body py-2">
                <div className="col px-1">

                    <div className="row justify-content-between mx-auto">
                        <div className="d-inline small font-weight-bold text-midnight">{header}</div>
                        <span className="badge badge-success px-4">Optimal</span>
                    </div>
 
                    <div className="row float-left align-text-top mx-auto mt-2">
                        <h1 className="d-inline text-left display-4">{content}</h1>
                        <h1 className="d-inline mx-1">°C</h1>
                    </div>

                </div>
            </div>
        </div>
    )

}

export default Card;