import React from 'react';
import Datatable from './datatable';

function Nav() {

    return (
        <div>
            <nav className="nav nav-tabs mb-2" id="nav-tab" role="tablist">
                <a className="nav-item nav-link text-midnight font-weight-bold small active" data-toggle="tab" href="#all" role="tab" aria-controls="all" aria-selected="true">
                    All Alarms</a>
                <a className="nav-item nav-link text-midnight font-weight-bold small" data-toggle="tab" href="#critical" role="tab" aria-controls="critical" aria-selected="false">
                    Critical Alarms</a>
                <a className="nav-item nav-link text-midnight font-weight-bold small" data-toggle="tab" href="#moderate" role="tab" aria-controls="moderate" aria-selected="false">
                    Moderate Alarms</a>
                <a className="nav-item nav-link text-midnight font-weight-bold small" data-toggle="tab" href="#low" role="tab" aria-controls="low" aria-selected="false">
                    Low Alarms</a>
            </nav>

            <div class="tab-content" id="nav-content">
                <div class="tab-pane show active" id="all" role="tabpanel" aria-labelledby="v-pills-critical-tab">
                    <Datatable /></div>
                <div class="tab-pane" id="critical" role="tabpanel" aria-labelledby="v-pills-critical-tab"></div>
                <div class="tab-pane" id="moderate" role="tabpanel" aria-labelledby="v-pills-moderate-tab"></div>
                <div class="tab-pane" id="low" role="tabpanel" aria-labelledby="v-pills-low-tab"></div>
            </div>

        </div>
    )
}

export default Nav;